rr) {\n        throw (err)\n      }\n    },\n    //油站图片详情\n    oliImgDetails (item) {\n      this.oliImgVisible = true\n      this.oliImgList = item.shop_image\n      this.$nextTick(() => {\n        const img = this.$refs.oliImg;\n        Viewer(img)\n      })\n    }\n  },\n  mounted () {\n    this.init()\n  }\n}\n</script>\n\n<style scoped>\n.ellipsis {\n  width: 100px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 2;\n  margin: 0 auto;\n  text-align: center;\n}\n</style>\n"],"sourceRoot":""}]);

